import throttle from 'lodash.throttle';
import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { applyMiddleware, createStore } from 'redux';
import thunkMiddleware from 'redux-thunk';

import App from './components/App';
import { getLocalStorageItem, setLocalStorageItem } from './utils/LocalStorageUtils';
import rootReducer from './reducers';

const STORE_KEY = 'translations-state';
const store = createStore(rootReducer, getLocalStorageItem(STORE_KEY), applyMiddleware(thunkMiddleware));
store.subscribe(throttle(() => {
  const { query, translations, ...state } = store.getState();
  setLocalStorageItem(STORE_KEY, state);
}, 1000));

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <App />
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);
