import debounce from 'lodash.debounce';
import { InputAdornment } from '@material-ui/core';
import { SearchOutlined } from '@material-ui/icons';
import React, { useCallback, useState } from 'react';
import { connect } from 'react-redux';
import { ThunkDispatch } from 'redux-thunk';

import { Action, fetchTranslations, setQuery } from '../actions';
import { RootState } from '../reducers';
import AppBarTextField from './AppBarTextField';

interface DispatchProps {
  onSubmit: (value: string) => void;
}

type Props = DispatchProps;

function QueryTextField(props: Props) {
  const [value, setValue] = useState('');
  const debouncedOnSubmit = useCallback(debounce(props.onSubmit, 500), [props.onSubmit]);
  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setValue(event.target.value);
    debouncedOnSubmit(event.target.value);
  }
  return (
      <AppBarTextField
          autoFocus fullWidth placeholder='Search' value={value} InputProps={{
            startAdornment: (
                <InputAdornment position='start'>
                  <SearchOutlined />
                </InputAdornment>
            ),
      }} onChange={handleChange} />
  );
}

function mapDispatch(dispatch: ThunkDispatch<RootState, void, Action>): DispatchProps {
  return {
    onSubmit(value) {
      dispatch(setQuery(value));
      dispatch(fetchTranslations());
    }
  }
}

export default connect(null, mapDispatch)(QueryTextField);
