import { combineReducers } from 'redux';

import { Action, ActionType } from '../actions';
import locales from '../constants/Locales';

function useDarkTheme(state: boolean = false, action: Action): boolean {
  switch (action.type) {
    case ActionType.TOGGLE_DARK_THEME:
      return !state;
    default:
      return state;
  }
}

function query(state: string = '', action: Action): string {
  switch (action.type) {
    case ActionType.SET_QUERY:
      return action.query;
    default:
      return state;
  }
}

const defaultLocale = locales.some(locale => locale.code === navigator.language) ? navigator.language : 'zh-CN';

function locale(state: string = defaultLocale, action: Action): string {
  switch (action.type) {
    case ActionType.SET_LOCALE:
      return action.locale;
    default:
      return state;
  }
}

function superproject(state: string = 'android', action: Action): string {
  switch (action.type) {
    case ActionType.SET_SUPERPROJECT:
      return action.superproject;
    default:
      return state;
  }
}

export interface Translations {
  isFetching: boolean;
  html: string;
}

function translations(state: Translations = { isFetching: false, html: '' }, action: Action): Translations {
  switch (action.type) {
    case ActionType.REQUEST_TRANSLATIONS:
      return Object.assign({}, state, {
        isFetching: true,
        html: '',
      });
    case ActionType.RECEIVE_TRANSLATIONS:
      return Object.assign({}, state, {
        isFetching: false,
        html: action.html,
      });
    default:
      return state;
  }
}

const rootReducer = combineReducers({
  useDarkTheme,
  query,
  locale,
  superproject,
  translations
});

export default rootReducer;

export type RootState = ReturnType<typeof rootReducer>;
