import { ThunkAction } from 'redux-thunk';

import { RootState } from '../reducers';
import { fetchTranslationTable } from './FetchTranslationTable';

export enum ActionType {
  TOGGLE_DARK_THEME = 'TOGGLE_DARK_THEME',
  SET_QUERY = 'SET_QUERY',
  SET_LOCALE = 'SET_LOCALE',
  SET_SUPERPROJECT = 'SET_SUPERPROJECT',
  REQUEST_TRANSLATIONS = 'REQUEST_TRANSLATIONS',
  RECEIVE_TRANSLATIONS = 'RECEIVE_TRANSLATIONS',
  DEFAULT = '__DEFAULT__'
}

interface ToggleDarkThemeAction {
  type: ActionType.TOGGLE_DARK_THEME;
}

export function toggleDarkTheme(): ToggleDarkThemeAction {
  return {
    type: ActionType.TOGGLE_DARK_THEME
  };
}

interface SetQueryAction {
  type: ActionType.SET_QUERY;
  query: string;
}

export function setQuery(query: string): SetQueryAction {
  return {
    type: ActionType.SET_QUERY,
    query,
  };
}

interface SetLocaleAction {
  type: ActionType.SET_LOCALE;
  locale: string;
}

export function setLocale(locale: string): SetLocaleAction {
  return {
    type: ActionType.SET_LOCALE,
    locale,
  };
}

interface SetSuperprojectAction {
  type: ActionType.SET_SUPERPROJECT;
  superproject: string;
}

export function setSuperproject(superproject: string): SetSuperprojectAction {
  return {
    type: ActionType.SET_SUPERPROJECT,
    superproject,
  };
}

interface RequestTranslationsAction {
  type: ActionType.REQUEST_TRANSLATIONS;
}

function requestTranslations(): RequestTranslationsAction {
  return { type: ActionType.REQUEST_TRANSLATIONS }
}

interface ReceiveTranslationsAction {
  type: ActionType.RECEIVE_TRANSLATIONS;
  html: string;
}

function receiveTranslations(html: string): ReceiveTranslationsAction {
  return {
    type: ActionType.RECEIVE_TRANSLATIONS,
    html,
  }
}

export function fetchTranslations(): ThunkAction<Promise<ReceiveTranslationsAction | null>, RootState, void, Action> {
  return async (dispatch, getState) => {
    const { query, locale, superproject } = getState();
    dispatch(requestTranslations());
    const html = await fetchTranslationTable(query, locale, superproject);
    return dispatch(receiveTranslations(html));
  }
}

interface DefaultAction {
  type: ActionType.DEFAULT;
}

export type Action = ToggleDarkThemeAction | SetQueryAction | SetLocaleAction | SetSuperprojectAction
    | RequestTranslationsAction | ReceiveTranslationsAction | DefaultAction
